/* eslint-disable spaced-comment */
/* eslint-disable no-else-return */
/* eslint-disable max-statements */
/* eslint-disable no-confusing-arrow */
/* eslint-disable import/no-cycle, max-lines */
import React, { useCallback, useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import Select, { Creatable, createFilter } from 'react-select';
import { Manager } from 'react-popper';
import { withContentRect } from 'react-measure';
import { cx, isEqual } from '@elliemae/ds-utilities/utils';
import theme from '@elliemae/ds-system/theme';
import usePrevious from '@elliemae/ds-utilities/hooks/usePrevious';
import useShouldRecalculate from '@elliemae/ds-utilities/hooks/useShouldRecalculate';

import { TooltipTextProvider } from '@elliemae/ds-basic/TruncatedTooltipText';
import withSelectStringValueConverter from '../v1/withSelectStringValueConverter';
import DropdownIndicator from './components/DropdownIndicator';
import GroupHeading from './components/GroupHeading';
import SelectMenu from './components/SelectMenu';
import Control from './components/Control';
import ClearIndicator from './components/ClearIndicator';
import MenuList from './components/MenuList';
import MultiValueLabel from './components/MultiValueLabel';
import MultiValueRemove from './components/MultiValueRemove';
import SingleValueLabel from './components/SingleValueLabel';
import LoadingIndicator from './components/LoadingIndicator';
import { calculateWidth } from './components/calculateWidth';
import { ValueContainer } from './components/ValueContainer';
import { DropDownContext } from './context';

const blockName = 'em-ds-combobox';
const container = `${blockName}-container`;
const getValues = (option, isMulti, valueProperty) => {
  if (!option) return null;
  return isMulti
    ? option.map((mOption) => mOption[valueProperty])
    : option[valueProperty];
};
const createOption = (label, value) => ({ label, value });

const DSComboBox2 = ({
  hideSelectedOptions = false,
  autoFocus = false,
  className = '',
  hasError = false,
  onFocus = () => null,
  onBlur = () => null,
  onChange = () => null,
  // onKeyDown = () => null,
  filterOption = createFilter({ ignoreAccents: false }), // this fix performance issue
  onClickDropdownIndicator = () => null,
  onInputKeyDown = () => null,
  onInputChange = () => null,
  isRtl = false,
  isFocused = undefined,
  isMulti = false,
  isFreeSolo,
  selectAllDisabled = false,
  formatCreateLabel,
  createOptionPosition,
  value: selectedValue = null,
  options = [],
  clearable = false,
  searchable = true,
  disabled = false,
  placeholder = '',
  valueProperty = 'value',
  labelProperty = 'label',
  loading = false,
  menuIsOpen = false,
  inlineMenu = false,
  components: customComponents = {},
  measureRef,
  contentRect,
  readOnly = false,
  returnValue = true,
  expandMenuToContainer = true,
  expandMenuOutsideContainer = false,
  customMenuItemOptions = {
    useTruncatedText: false,
    itemSize: 35,
  },
  containerProps = {},
  maxOptions,
  zIndex = 11,
  componentsStyle,
  keepTypedValue = true, //TEMPORARY PROP UNTIL WE HAVE THIS FUNCTIONALITY IN SINGLE FREE SOLO
  ...restPropsToCustomizeSelect
}) => {
  const select = useRef();
  const [selectAll, setSelectAll] = useState(false);
  const [inputInFocus, setFocus] = useState(false);
  const [value, setValue] = useState(selectedValue);
  const [inputValue, setInputValue] = useState('');
  const [handleOpening, setHandleOpening] = useState(false);
  const [menuOpen, setMenuOpen] = useState(menuIsOpen);
  const [isMultipleKeyPressed, setIsMultipleKeyPressed] = useState(false);
  const prevOptions = usePrevious(options);
  const optionsChanged = useShouldRecalculate(isEqual(prevOptions, options));
  const SelectComponent = isFreeSolo ? Creatable : Select;

  useEffect(() => {
    setValue(selectedValue);
  }, [selectedValue]);

  useEffect(() => {
    if (value) {
      const newValue = options.find((option) => value.value === option.value);
      setValue(newValue);
    }
  }, [optionsChanged]);

  const onSelectAll = useCallback((isSelected) => {
    setSelectAll(isSelected);
    if (isSelected) {
      onChange(getValues(options, isSelected, valueProperty));
    } else {
      onChange(getValues());
    }
  }, []);

  const handleInputChange = useCallback((val, action) => {
    setInputValue(val);
    if (action.action === 'input-change' && !isMulti) {
      setMenuOpen(true);
      if (!inlineMenu && keepTypedValue) {
        onChange({ value: val, label: val }, action);
      }
    }
    if (val) onInputChange(val, action);
  }, []);

  const handleOnKeyDown = (e) => {
    if (e.key === 'Backspace' && !e.target.value && value) {
      onChange('', { action: 'input-clear' }); // this clears the input
    }
    if (e.key === 'Control') setIsMultipleKeyPressed(true);
    // If Ctrl + Enter are pressed, focus is set to the input and the menu closes
    if (isMultipleKeyPressed && e.key === 'Enter') {
      setFocus(true);
      setMenuOpen(false);
      setIsMultipleKeyPressed(false);
      e.preventDefault();
    }
  };

  const handleOnChange = (newValue, action) => {
    setMenuOpen(isMulti);
    if (!isMulti) select.current.blur();
    if (returnValue) {
      if (isFreeSolo) return onChange(newValue, action);
      return onChange(getValues(newValue, isMulti, valueProperty), action);
    }
    if (isMulti && selectAll) return onChange(options, action);
    return onChange(newValue, action);
  };

  const handleCreateNewOption = (inputVal) => {
    const newOption = createOption(inputVal, inputVal);
    const prevValue = Array.isArray(value) ? value : [];
    const newValue = isMulti ? [...prevValue, newOption] : newOption;
    if (!isMulti) select.current.blur();
    setMenuOpen(isMulti);
    onChange(newValue);
  };

  const handleFocus = useCallback((e) => {
    setFocus(true);
    if (!inlineMenu) setMenuOpen(true);
    onFocus(e);
    setHandleOpening(true);
  }, []);

  const handleBlur = (e) => {
    setFocus(false);
    if (!inlineMenu) setMenuOpen(false);
    onBlur(e, value);
    setHandleOpening(false);
  };

  return (
    <TooltipTextProvider>
      <DropDownContext.Provider
        value={{
          inputInFocus,
          value,
          isMulti,
          menuOpen,
          handleOpening,
          selectAll,
          selectAllDisabled,
          changeHandleOpening: (isOpen) => setHandleOpening(isOpen),
          changeMenuOpen: (isOpen) => setMenuOpen(isOpen),
          onClickDropdownIndicator,
          onSelectAll: (val) => onSelectAll(val),
          clearable,
          removeDropdownIndicator: customComponents.DropdownIndicator === null,
          select,
        }}
      >
        <Manager>
          <div
            ref={measureRef}
            className={cx(
              `${container}`,
              inputInFocus && 'in-focus',
              hasError && 'with-error',
              disabled && 'is-disabled',
              className,
            )}
            data-testid="combobox"
            {...containerProps}
          >
            <SelectComponent
              ref={select}
              autoFocus={autoFocus}
              classNamePrefix={blockName}
              // If it is multiselect let the menu open on select.
              closeMenuOnSelect={!isMulti}
              components={{
                Control,
                // IndicatorSeparator,
                Menu: SelectMenu,
                DropdownIndicator,
                ClearIndicator,
                GroupHeading,
                MultiValueLabel,
                MultiValueRemove,
                SingleValue: SingleValueLabel,
                MenuList,
                ValueContainer,
                ...customComponents,
              }}
              customMenuItemOptions={{ maxOptions, ...customMenuItemOptions }}
              expandMenuOutsideContainer={expandMenuOutsideContainer}
              expandMenuToContainer={expandMenuToContainer}
              filterOption={filterOption}
              getOptionLabel={(option) => option[labelProperty]}
              getOptionValue={(option) => option[valueProperty]}
              formatCreateLabel={formatCreateLabel}
              createOptionPosition={createOptionPosition}
              hideSelectedOptions={hideSelectedOptions}
              inlineMenu={inlineMenu}
              isClearable={clearable}
              isDisabled={disabled || readOnly}
              isFocused={isFocused}
              isMulti={isMulti}
              isRtl={isRtl}
              isSearchable={readOnly ? false : searchable}
              menuIsOpen={menuOpen}
              noOptionsMessage={() =>
                loading ? <LoadingIndicator /> : 'No matches found'
              }
              onBlur={handleBlur}
              onChange={handleOnChange}
              onCreateOption={handleCreateNewOption}
              onFocus={handleFocus}
              onInputChange={handleInputChange}
              onInputKeyDown={onInputKeyDown}
              onKeyDown={handleOnKeyDown}
              options={loading ? [] : options}
              placeholder={placeholder}
              selectMeasure={contentRect}
              styles={{
                control: () => null,
                option: () => null,
                dropdownIndicator: () => null,
                groupHeading: (provided) => ({ ...provided }),
                clearIndicator: () => null,
                placeholder: () => null,
                indicatorSeparator: () => null,
                singleValue: (provided) => ({ ...provided, maxWidth: '90%' }),
                multiValueLabel: (provided) => ({
                  ...provided,
                  backgroundColor: 'transparent',
                }),
                multiValue: (provided) => ({
                  ...provided,
                  backgroundColor: 'transparent',
                }),
                multiValueRemove: (provided) => ({
                  ...provided,
                  '&:hover': {
                    backgroundColor: 'transparent',
                    color: theme.colors.neutral[800],
                  },
                }),
                valueContainer: (provided) => ({
                  ...provided,
                  flexWrap: 'nowrap',
                }),
                menu: () => {
                  if (expandMenuOutsideContainer) {
                    const width = calculateWidth(options);
                    if (!width) return null;
                    return { width: `${width}px` };
                  }
                  if (loading) return { height: '72px' };
                  return null;
                },
                ...componentsStyle,
              }}
              tabSelectsValue={false}
              value={value}
              inputValue={inputValue}
              zIndex={zIndex}
              {...restPropsToCustomizeSelect}
            />
          </div>
        </Manager>
      </DropDownContext.Provider>
    </TooltipTextProvider>
  );
};

DSComboBox2.propTypes = {
  containerProps: PropTypes.object,
  handleHoverRef: PropTypes.func,
  /**
   * Whether the combo box uses auto focus or not
   */
  autoFocus: PropTypes.bool,
  className: PropTypes.string,
  /**
   * Whether the combo box has error or not
   */
  hasError: PropTypes.bool,
  /**
   * Allows a function that is triggered once the combo box is focused
   */
  onFocus: PropTypes.func,
  /**
   * Allows a function that is triggered once the combo box loses focus
   */
  onBlur: PropTypes.func,
  /**
   * Allows a function that is triggered once the combo box changes
   */
  onChange: PropTypes.func,
  /**
   * Allows a function that is triggered once a key is being pressed
   */
  onKeyDown: PropTypes.func,
  /**
   * Allows a function that is triggered once the combo box input key down
   */
  onInputKeyDown: PropTypes.func,
  /**
   * Allows a function that is triggered once the combo box input changes
   */
  onInputChange: PropTypes.func,
  /**
   * Whether the combo box is rtl or not  //ASK
   */
  isRtl: PropTypes.bool,
  /**
   * Whether the combo box is focused or not
   */
  isFocused: PropTypes.bool,
  /**
   * Whether the select all/clear buttons are enabled or not
   */
  selectAllDisabled: PropTypes.bool,
  /**
   * Whether the combo box is multi or not
   */
  isMulti: PropTypes.bool,
  inputValue: PropTypes.string,
  showAllOption: PropTypes.bool,
  allOption: PropTypes.shape({
    label: PropTypes.string,
    value: PropTypes.string,
  }),
  /**
   * Value that the combo box has as default
   */
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.object,
  ]),
  /**
   * Value that the combo box has as default
   */
  options: PropTypes.array,
  /**
   * Whether the combo box is clearable or not
   */
  clearable: PropTypes.bool,
  /**
   * Whether the combo box is searchable or not. Set to false makes the input non-editable
   */
  searchable: PropTypes.bool,
  /**
   * Whether the combo box is disabled or not
   */
  disabled: PropTypes.bool,
  /**
   * Placeholder for the combo box
   */
  placeholder: PropTypes.string,
  /**
   * Displays a Loading Indicator in the menu
   */
  loading: PropTypes.bool,
  valueProperty: PropTypes.string,
  labelProperty: PropTypes.string,
  /**
   * Whether the combo box menu is open or not
   */
  menuIsOpen: PropTypes.bool,
  /**
   * Whether to show the combo box menu inline or not
   */
  inlineMenu: PropTypes.bool,
  /**
   * Message to show once there aren't any options
   */
  noOptionsMessage: PropTypes.func,
  /**
   * Components you can add to the combo box
   */
  measureRef: PropTypes.func,
  /**
   * Whether the combo box is read only or not
   */
  readOnly: PropTypes.bool,
  /**
   * Whether the combo box can be expanded the menu to container or not
   */
  expandMenuToContainer: PropTypes.bool,
  /**
   * Allow have options larger than his container
   */
  expandMenuOutsideContainer: PropTypes.bool,
  /**
   * Custom combo box menu item options
   */
  customMenuItemOptions: PropTypes.object,
  /**
   * Props to enable multiple free solo combobox
   */
  multipleFreeSoloProps: PropTypes.object,
  /**
   * Whether the combo box is has value to return or not
   */
  returnValue: PropTypes.bool,
  hideSelectedOptions: PropTypes.bool,
  maxOptions: PropTypes.number,
  /**
   * Custom method to filter whether an option should be displayed in the menu
   */
  filterOption: PropTypes.func,
  isFreeSolo: PropTypes.bool,
  formatCreateLabel: PropTypes.func,
  createOptionPosition: PropTypes.string,
  onClickDropdownIndicator: PropTypes.func,
  /**
   * Object with custom components for react-select
   */
  components: PropTypes.object,
  componentsStyle: PropTypes.object,
  contentRect: PropTypes.object,
  zIndex: PropTypes.number,
  keepTypedValue: PropTypes.bool,
};

export default withSelectStringValueConverter(
  withContentRect('bounds')(DSComboBox2),
);
